import React from 'react'
import history from '../history/history'

 export const Context = React.createContext()

export const ContextProvider = ({children}) => {

 const slider = (path)=>{


    setTimeout(() => {
       history.push(path) 
    }, 2000);




 }



    return (
        <Context.Provider value={slider} >
            {children}
        </Context.Provider>
    )
}


